.password-generator__container {
	display: flex;
	align-items: center;
	justify-content: center;
	min-height: 100vh;
}

.p-generator {
	display: flex;
	align-items: center;
	justify-content: center;
	color: $color-white;

	input[type='checkbox'] {
		display: none;
	}

	h1 {
		margin-bottom: 30px;
	}

	&__wrapper {
		padding: 30px;
		background-color: $color-dark;
		border-radius: 20px;
		min-width: 450px;
	}

	&__content {
		display: flex;
		flex-direction: column;
	}

	&__range-length {
		font-style: normal;
	}

	&__password {
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: $br-input;
		height: 80px;
		width: 100%;
		padding: $p-input-x;
		text-align: center;
		font-size: 18px;
		letter-spacing: 0.3px;
		background-color: $color-gray;
		color: $color-white;
		pointer-events: none;

		&:focus {
			outline: none;
		}
	}

	&__password-label {
		position: relative;
		margin-bottom: 30px;
	}

	&__password-copy {
		position: absolute;
		right: 0;
		bottom: 0;
		transform: translate(-40%, -40%);
		color: $color-white;
		cursor: pointer;
	}

	&__range-slider {
		position: relative;
		display: flex;
		justify-content: center;
		padding: $p-input-y $p-input-x;
		background-color: $color-gray;
		border-radius: $br-input;
		color: $color-white;
		margin-bottom: 20px;

		&:before {
			content: attr(data-min);
			position: absolute;
			left: 0;
			top: 50%;
			font-size: 18px;
			transform: translateY(-50%) translateX(20px);
		}

		&:after {
			content: attr(data-max);
			position: absolute;
			right: 0;
			top: 50%;
			font-size: 18px;
			transform: translateY(-50%) translateX(-20px);
		}

		input {
			height: 3px;
			width: calc(100% - 100px);
		}
	}

	&__range-slider-wrapper,
	&__settings {
		> span {
			display: block;
			margin-bottom: 3px;
			text-transform: uppercase;
			font-size: 12px;
		}
	}

	&__settings-items {
		display: flex;
		flex-direction: column;
		row-gap: 10px;
		margin-bottom: 20px;
	}

	&__settings-item {
		padding: $p-input-y $p-input-x;
		display: flex;
		align-items: center;
		justify-content: space-between;
		background-color: $color-gray;
		border-radius: $br-input;
		color: $color-white;
		font-size: 18px;

		> span {
			padding-left: 15px;
		}
	}

	&__input-label {
		position: relative;
		height: 23px;
		width: 23px;
		border-radius: 2px;
		overflow: hidden;
		cursor: pointer;

		&::before {
			content: '';
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			background-color: #fff;
		}

		&::after {
			content: '';
			position: absolute;
			left: 50%;
			top: 50%;
			width: 12px;
			height: 12px;
			background-color: $color-dark;
			border-radius: 2px;
			transform: translate(-50%, -50%);
			opacity: 0;
			transition: opacity 0.1s ease;
		}
	}

	&__input-label.active {
		&:after {
			opacity: 1;
		}
	}

	&__button-generate {
		padding: 20px 0px;
		border-radius: $br-input;
		background-color: rgba($color: #0e0e0e, $alpha: 0.8);
		color: $color-white;
		font-size: 18px;
		text-transform: uppercase;
		letter-spacing: 2px;
		transform: translateY(0);

		&:active {
			transform: translateY(-1px);
		}
	}
}
